import { MenuItem, SubMenu } from "react-contextmenu";
import PerfectScrollbar from 'react-perfect-scrollbar';
import SVG from 'react-inlinesvg';
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { GET_MEDIA_NAME } from '../../../../../Services/Utils/utils';
const ContextMenus = ({ collections, postPostCopy, editMediaModalShow, handleModalShow, asset, rightsHandleModalShow, exportDonwloadMedia, postPostMove, deleteAsset, history, addTagModalShow, isCollection }) => {
    return <><div className="position-relative">
        <div className="menu-item px-2">
            <div className="menu-content text-muted px-3 fs-8 text-uppercase">Choose your actions</div>
        </div>
        <div className="menu-item" onClick={editMediaModalShow(asset)}>
            <MenuItem className="dropdown-item">
                <span className="svg-icon svg-icon-5">
                    <SVG src={baseUrl('media/icons/svg-icons/magic-stick.svg')} height={14} width={14} />
                </span>
                <span className="menu-title">Edit</span>
            </MenuItem>
        </div>
        <div className="menu-item" onClick={handleModalShow(asset)}>
            <MenuItem className="dropdown-item">
                <i className="ri-information-line icon-6 me-1"></i>
                <span className="menu-title">View Details</span>
            </MenuItem>
        </div>
        {asset.Feed && Object.keys(asset.Feed).length > 0 && (asset.networkId == 1 || (asset.networkId == 3 && asset.Feed.filterId == 62) || asset.networkId == 7 || asset.networkId == 8 || asset.networkId == 6 || asset.networkId == 11 || asset.networkId == 21 || (asset.networkId == 18 && (asset.Feed.filterId == 24 || asset.Feed.filterId == 25 || asset.Feed.filterId == 26)) || asset.networkId == 28) && asset.ugcstatus == null && asset.type != 1 ? <div className="menu-item" onClick={rightsHandleModalShow(asset)}>
            <MenuItem className="dropdown-item">
                <span className="svg-icon svg-icon-5">
                    <SVG src={baseUrl('media/icons/svg-icons/rights-line.svg')} height={14} width={14} />
                </span>
                <span className="menu-title">Get Rights</span>
            </MenuItem>
        </div> : null}
        <div className="menu-item" onClick={addTagModalShow(asset)}>
            <MenuItem className="dropdown-item">
                <i className="ri-price-tag-3-line icon-6 me-1"></i>
                <span className="menu-title">Add Tag</span>
            </MenuItem>
        </div>
        <div className="menu-item">
            <SubMenu className={`dropdown-menu px-0 show`} title={
                <MenuItem className="dropdown-item dropdown-arrow"
                    preventClose={true}>
                    <i className="ri-file-copy-line icon-6 me-1"></i>
                    <span className="menu-title">Copy</span>
                </MenuItem>
            }>
                <PerfectScrollbar options={{ wheelPropagation: false }}>
                    <div className="submenu_contxt_wrap submenu_contxt_lg">
                        {collections && collections.length > 0 ?
                            collections.map((cItem, cotInx) =>
                                <div key={`conMenu_${cotInx}`} className="menu-item text-decoration-none">
                                    <MenuItem className="dropdown-item py-1 px-2" onClick={postPostCopy({ destination: String(cItem.id), collection: String(asset.collection), id: asset.id })}>
                                        <div className="symbol symbol-24 flex-shrink-0 me-2 bg-light" style={{ borderRadius: 2 }}>
                                            {cItem.thumbnails ? <img className="img-fluid object-center-cover" src={cItem.thumbnails} height={54} width={54} alt={GET_MEDIA_NAME(cItem.thumbnails)} /> : <i className="ri-folder-open-line me-0"></i>}
                                        </div>
                                        <span className="menu-title">{cItem.name}</span>
                                    </MenuItem>
                                </div>
                            ) : null}
                    </div>
                </PerfectScrollbar>
            </SubMenu>

        </div>

        <div className="menu-item text-decoration-none">
            <SubMenu className={`dropdown-menu px-0 show`} title={
                <MenuItem className="dropdown-item dropdown-arrow"
                    preventClose={true}>
                    <i className="ri-arrow-right-circle-line icon-6 me-1"></i>
                    <span className="menu-title">Move to</span>
                </MenuItem>
            }>
                <PerfectScrollbar options={{ wheelPropagation: false }}>
                    <div className="submenu_contxt_wrap submenu_contxt_lg">
                        {collections && collections.length > 0 ?
                            collections.map((cItem, subindx) => {
                                return (!(isCollection && asset.collection && String(asset.collection).includes(cItem.id))) ? <div key={`subMenu_${subindx}`} className="menu-item text-decoration-none">
                                    <MenuItem className="dropdown-item py-1 px-2" onClick={postPostMove(asset.id, asset.collection, cItem.id)}>
                                        <div className="symbol symbol-24 flex-shrink-0 me-2 bg-light" style={{ borderRadius: 2 }}>
                                            {cItem.thumbnails ? <img className="img-fluid object-center-cover" src={cItem.thumbnails} height={54} width={54} alt={GET_MEDIA_NAME(cItem.thumbnails)} /> : <i className="ri-folder-open-line me-0"></i>}
                                        </div>
                                        <span className="menu-title">{cItem.name}</span>
                                    </MenuItem>
                                </div> : null
                            }
                            ) : null}
                    </div>
                </PerfectScrollbar>
            </SubMenu>
        </div>
        <div className="menu-item">
            <SubMenu className={`dropdown-menu px-0 show`} title={
                <MenuItem className="dropdown-item dropdown-arrow"
                    preventClose={true}>
                    <span className="svg-icon svg-icon-5">
                        <SVG src={baseUrl('media/icons/svg-icons/publish-line.svg')} height={14} width={14} />
                    </span>
                    <span className="menu-title">Publish to</span>
                </MenuItem>
            }>
                <PerfectScrollbar options={{ wheelPropagation: false }}>
                    <div className="submenu_contxt_wrap">
                        <div className="menu-item text-decoration-none">
                            <MenuItem className="dropdown-item" onClick={event => history(`/social-publish?assetId=${asset.id}`)}>
                                <i className="ri-navigation-line icon-6 me-1" style={{transform:'scaleX(-1)'}}></i>
                                <span className="menu-title">Social Publish</span>
                            </MenuItem>
                        </div>
                        <div className="menu-item text-decoration-none">
                            <MenuItem className="dropdown-item" onClick={event => history(`/print-media?assetId=${asset.id}`)}>
                                <span className="svg-icon svg-icon-5">
                                    <SVG src={baseUrl('media/icons/svg-icons/print-media-2.svg')} height={15} width={15} />
                                </span>
                                <span className="menu-title">Print Media</span>
                            </MenuItem>
                        </div>
                        <div className="menu-item text-decoration-none">
                            <MenuItem className="dropdown-item disabled" onClick={event => history('/social-ads')}>
                                <span className="svg-icon svg-icon-5">
                                    <SVG src={baseUrl('media/icons/svg-icons/speaker-line.svg')} height={15} width={15} />
                                </span>
                                <span className="menu-title">Social Ads</span>
                            </MenuItem>
                        </div>
                    </div>
                </PerfectScrollbar>
            </SubMenu>

        </div>
        <div className="menu-item text-decoration-none" onClick={exportDonwloadMedia(asset.postFile)}>
            <MenuItem className="dropdown-item">
                <i className="ri-download-2-line icon-6 me-1"></i>
                <span className="menu-title">Export/Download</span>
            </MenuItem>
        </div>
        <div className="menu-item">
            <MenuItem className="dropdown-item" onClick={deleteAsset(asset)}>
                <i className="ri-delete-bin-6-line icon-6 me-1"></i>
                <span className="menu-title">Move to Trash</span>
            </MenuItem>
        </div>
    </div></>
}
export default ContextMenus