import React, { Component } from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
export default class MediaOptions extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    render() {
        const { postPinToTop, asset, rightsHandleModalShow, onClickToSingleCheck, updateIndex, postStatusUpdate } = this.props
        return <div className="d-flex align-items-center">
            <label className="form-check me-2">
                <input className="form-check-input" type="checkbox" name="" value="phone" checked={asset.isChecked} onChange={onClickToSingleCheck(asset.id, updateIndex)} />
            </label>
            <OverlayTrigger placement="top" overlay={<Tooltip>Pin To Top</Tooltip>}>
                <button type="button" className="btn p-0 border-0 me-2 d-flex" onClick={postPinToTop(asset)}>
                    <i className={`ri-pushpin-2-fill icon-6 me-0 ${asset.pin == 1 ? 'text-success' : 'text-gray-800'}`}></i>
                </button>
            </OverlayTrigger>
            {asset.Feed && Object.keys(asset.Feed).length > 0 && (asset.networkId == 1 || (asset.networkId == 3 && asset.Feed.filterId == 62) || asset.networkId == 7 || asset.networkId == 8 || asset.networkId == 6 || asset.networkId == 11 || asset.networkId == 21 || (asset.networkId == 18 && (asset.Feed.filterId == 24 || asset.Feed.filterId == 25 || asset.Feed.filterId == 26)) || asset.networkId == 28) && asset.ugcstatus == null && asset.type != 1 ? <OverlayTrigger placement="top" overlay={<Tooltip>Get Rights</Tooltip>}>
                <button type="button" className="btn p-0 border-0 me-2" onClick={rightsHandleModalShow(asset)}>
                    {asset.ugcRights == null ? <span className="svg-icon svg-icon-6 me-0 text-gray-800">
                        <SVG src={baseUrl('media/icons/svg-icons/get-rights.svg')} height={16} width={16} />
                    </span> : asset.ugcRights.status == 2 ? <span className="svg-icon svg-icon-6 me-0 text-success">
                        <SVG src={baseUrl('media/icons/svg-icons/rights-approved-fill.svg')} height={16} width={16} />
                    </span> : asset.ugcRights.status == 1 ? <span className="svg-icon svg-icon-6 me-0 text-warning">
                        <SVG src={baseUrl('media/icons/svg-icons/rights-pending-fill.svg')} height={16} width={16} />
                    </span> : asset.ugcRights.status == 3 ? <span className="svg-icon svg-icon-6 me-0 text-danger">
                        <SVG src={baseUrl('media/icons/svg-icons/rights-rejected-fill.svg')} height={16} width={16} />
                    </span> : null}
                </button>
            </OverlayTrigger> : null}
            <OverlayTrigger placement="top" overlay={<Tooltip>{asset.status == 2 ? 'Show':'Hide'}</Tooltip>}>
                <button type="button" className="btn p-0 border-0 me-2 d-flex" onClick={()=>postStatusUpdate(asset)}>
                    <i className={`icon-5 me-0 ${asset.status == 2 ? 'ri-eye-off-line' : 'ri-eye-line'}`}></i>
                </button>
            </OverlayTrigger>
        </div>
    }
}
