import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_MEDIA_NAME, GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../Services/Utils/utils';

export default class Video extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
      // const image = await loadImage(imgUrl);
      // this.setState({
      //     height: (image.height * 100) / image.width,
      //     width: (image.width * 100) / image.height
      // })
    }
  }
  onErrorImageUpdate = event => {
    event.target.src = baseUrl(`media/images/error/image.svg`)
  }

  render() {
    const { socialNetwork, asset } = this.props
    const { imgUrl } = this.state

    const imageStyle = {
      paddingBottom: '100%', width: '100%'
    };
    const videoThumb =  asset.networkId != 12 ? asset.postFile : asset.picture;

    return <div className="position-relative w-100" style={imageStyle}>
            <img className="position-absolute object-center-cover" loading="lazy" src={videoThumb} height={260} width={260} alt={GET_MEDIA_NAME(imgUrl)} onError={this.onErrorImageUpdate} />

          <div className="position-absolute end-0 top-0 p-2">
            {asset.networkId != 12 ?
            <i className="ri-vidicon-fill icon-5 text-light-alt assets_video_ico"></i>
            : <i className="ri-volume-up-fill icon-5 text-light-alt audio_icon"></i> }
          </div>
          <div className="position-absolute p-2 end-0 bottom-0">
            <img className="img-fluid" loading="lazy" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(socialNetwork && socialNetwork.length > 0 ? socialNetwork[0].id : 0)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(socialNetwork && socialNetwork.length > 0 ? socialNetwork[0].id : 0)} icon-data={GET_NETWORK_NAME(socialNetwork && socialNetwork.length > 0 ? socialNetwork[0].id : 0)} />
          </div>
        </div>
  }
}
