import React, { Component, Suspense } from 'react';
import { toast } from "react-toastify";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import { saveAs } from "file-saver";
import { ContextMenu, ContextMenuTrigger } from "react-contextmenu";
import Select from 'react-select';
import Media from './Elements/Media';
import Swal from 'sweetalert2';
import { postMoveToTrash, MoveToTrashAll, CopyAll, MoveToAll, ClearSelection, onAssetToAllReSelect, statusUpdateAll } from '../../../../Services/Actions/assetsActions'
import { uploadModalClick } from '../../../../Services/Actions/homeActions'
import MediaOptions from './Elements/MediaOptions';
import ContextMenus from './Elements/ContextMenus';
const FilterAside = React.lazy(() => import('./FilterAside'));
const AssetsModal = React.lazy(() => import('../AssetsModal/AssetsModal'));
const EditMediaModal = React.lazy(() => import('../EditMediaModal/EditMediaModal'));
const GetRightsModal = React.lazy(() => import('../GetRightsModal/GetRightsModal'));
const AddTagModal = React.lazy(() => import('../AddTagModal/AddTagModal'));
const NoAssetsFound = React.lazy(() => import('./Elements/NoAssetsFound'));
const Pagination = React.lazy(() => import('./Pagination'));
const SelectAllOptions = React.lazy(() => import('./Elements/SelectAllOptions'));


class Masonry extends Component {
    state = { imagesData: [], page: 0, showModal: false, rightsShowModal: false, editMediaShowModal: false, addTagShowModal: false, modalAssetData: null, selectAll: false, bulkRightsData: null, currentIndex: null };

    handleModalShow = (asset, index) => event => this.setState({
        showModal: true, modalAssetData: asset, currentIndex: index
    }, () => this.props.onAssetToAllReSelect())
    handleModalShowC = (e, data) => {
        this.setState({
            showModal: true, modalAssetData: data
        })
    }
    handleCallback = (dataEvent) => {
        this.setState({
            showModal: dataEvent, modalAssetData: dataEvent ? this.state.modalAssetData : null, currentIndex: null
        })
    }

    slideAssets = (currentIndex) => event => {
        const { myAssetsData } = this.props
        if (currentIndex === -1) {
            let count = myAssetsData.assets.length - 1
            this.setState({ currentIndex: count, modalAssetData: myAssetsData.assets[count] });
        }
        else {
            let count = myAssetsData.assets.length == currentIndex ? 0 : currentIndex
            this.setState({ currentIndex: count, modalAssetData: myAssetsData.assets[count] });
        }

    };

    rightsHandleModalShow = (asset) => event => {
        if (!asset) {
            const { myAssetsData } = this.props
            var textpost = false; var allowugc = false; var allowugcstatus = false; var networkchk = false; var isSameNetwork = false;
            var newData = []

            Object.keys(myAssetsData.selectedData).map((item) => {
                const filterdData = myAssetsData.assets.filter((fltr) => fltr.id == item)
                if (filterdData && filterdData.length > 0) {
                    newData.push(filterdData[0])
                }
            })
            newData.map((grItem) => {
                if (grItem.type == 1) textpost = true;
                else if (grItem.ugcRights && (grItem.ugcRights.status == 1 || grItem.ugcRights.status == 2)) allowugcstatus = true;
                else if (grItem.Feed && Object.keys(grItem.Feed).length > 0 && (grItem.networkId == 1 || (grItem.networkId == 3 && grItem.Feed.filterId == 62) || grItem.networkId == 7 || grItem.networkId == 8 || grItem.networkId == 6 || grItem.networkId == 11 || grItem.networkId == 21 || (grItem.networkId == 18 && (grItem.Feed.filterId == 24 || grItem.Feed.filterId == 25 || grItem.Feed.filterId == 26)) || grItem.networkId == 28)) {
                    allowugc = true;
                } else {
                    networkchk = true;
                }

                if (isSameNetwork == false && grItem.networkId != 18) {
                    isSameNetwork = true;
                }
            })
            if (textpost) {
                toast.error('UGC Rights request works for media posts. Please remove text posts from your selection.')
                return;
            }
            else if (allowugcstatus) {
                toast.error('You cannot send request on approved/pending post!!')
                return;
            }
            else if (allowugc == false && networkchk == true) {
                toast.error('You cannot send request!!')
                return;
            }
            else if (newData.length > 10) {
                toast.error('You cannot select user post more than 10!');
                return;
            }
            this.setState({
                rightsShowModal: true,
                modalAssetData: newData[0],
                bulkRightsData: newData
            })
        }
        else {
            this.setState({
                rightsShowModal: true,
                modalAssetData: asset,
                bulkRightsData: null
            })
        }


    }

    rightsHandleCallback = (rightsEvent) => {
        this.setState({
            rightsShowModal: rightsEvent,
            modalAssetData: null,
            bulkRightsData: null
        })
    }

    editMediaModalShow = (asset) => event => {
        this.setState({
            editMediaShowModal: true,
            modalAssetData: asset
        })
    }

    editMediaCallback = (editModalEvent) => event => {
        this.setState({
            editMediaShowModal: editModalEvent,
            modalAssetData: null
        })
    }

    addTagModalShow = (data) => event => {
        this.setState({
            addTagShowModal: true,
            modalAssetData: data
        })
    }
    addTagCallback = (addTagEvent) => {
        this.setState({
            addTagShowModal: addTagEvent,
            modalAssetData: null
        })
    }

    updateAssetStatus = () => {
        var { modalAssetData } = this.state;
        this.setState(prevState => ({ modalAssetData: { ...prevState.modalAssetData, status: modalAssetData.status == 1 ? 2 : 1 } }), () => this.props.postStatusUpdate(modalAssetData))
    }

    deleteAsset = (asset) => event => {
        const { postMoveToTrash } = this.props
        Swal.fire({
            title: 'Are you sure?',
            text: "Asset are stored in the trash for 30 days before being permanently deleted.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                postMoveToTrash({
                    "action": "5",
                    "source": "",
                    "destination": "",
                    "postId": String(asset.referenceId),
                    "postFile": "",
                    "tags": "",
                    "content": ""
                }, () => {
                    // toast.success("Asset has been deleted.")
                })

            }
        })
    }

    checkSelectAll = () => {
        this.setState({
            selectAll: !this.state.selectAll
        })
    }

    exportDonwloadMedia = (postFile) => event => {
        saveAs(postFile, `${uuidv4()}.jpg`);
    }


    onSelectToMove = (destination) => event => this.props.MoveToAll(destination, () => this.props.onLoadCollectionList())
    onSelectToTags = (tagsString) => event => {

    }
    onSelectToCopy = (destination) => event => this.props.CopyAll(destination, () => this.props.onLoadCollectionList())
    onSelectMoveToTrash = event => this.props.MoveToTrashAll(() => this.props.onLoadCollectionList())
    onSelectPostStatus = (status) => event => this.props.statusUpdateAll(status)
    onClickToOpenUpload = event => {
        const { uploadStatus } = this.props
        this.props.uploadModalClick(uploadStatus ? false : true)
    }
    onUpdateRejectedRightDetail = event => {
        var { modalAssetData } = this.state
        if (modalAssetData && modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0) {
            modalAssetData.ugcRights = null
            this.setState({ modalAssetData })
        }
    }

    render() {
        const { imagesData, showModal, rightsShowModal, editMediaShowModal, addTagShowModal, modalAssetData, selectAll, bulkRightsData } = this.state;
        const { myAssetsData, setCurrentPage, perPage, currentPage, setPageLimit, pageLimitOption, networkData, postPinToTop, postPostMove, postPostCopy, postPostSaveCopy, postPostUpdate, clickToCheckStatusAll, collections, onClickToSingleCheck, isCollection, postStatusUpdate } = this.props
        return <>
            {myAssetsData && myAssetsData.assets && myAssetsData.assets.length > 0 ?
                <div className="d-flex align-items-end justify-content-between flex-wrap">
                    <div className="d-flex align-items-center">
                        <label className="form-check my-3 flex-shrink-0 me-3 align-items-center" style={{ minHeight: 23 }}>
                            <input className="form-check-input" type="checkbox" name="select-all" onChange={clickToCheckStatusAll} checked={myAssetsData.isSelectAll} />
                            <span className="form-check-label" style={{ minWidth: 98 }}>Select All ({Object.keys(myAssetsData.selectedData).length})</span>
                        </label>
                        <SelectAllOptions myAssetsData={myAssetsData} addTagModalShow={this.addTagModalShow} collections={collections} postPostMove={postPostMove} onSelectToMove={this.onSelectToMove} onSelectToTags={this.onSelectToTags} onSelectToCopy={this.onSelectToCopy} onSelectMoveToTrash={this.onSelectMoveToTrash} rightsHandleModalShow={this.rightsHandleModalShow} onSelectPostStatus={this.onSelectPostStatus} />
                    </div>
                    <div className="d-flex align-items-center flex-wrap enteries_count">
                        <div className="block-center flex-shrink-0 me-2 my-2">
                            <span className="me-1">Show</span>
                            <Select className="selectpicker selectpicker-sm"
                                classNamePrefix="selectpicker"
                                value={pageLimitOption}
                                isLoading={false}
                                isClearable={false}
                                isSearchable={false}
                                onChange={setPageLimit}
                                options={[{ value: '25', label: '25' },
                                { value: '50', label: '50' },
                                { value: '100', label: '100' },
                                { value: '200', label: '200' }
                                ]} />
                            <span className="ms-1">entries</span>
                        </div>
                        {myAssetsData && Object.keys(myAssetsData).length > 0 ? <Pagination
                            itemsCount={myAssetsData.total}
                            itemsPerPage={perPage}
                            currentPage={currentPage}
                            setCurrentPage={setCurrentPage}
                            alwaysShown={false} /> : null}
                    </div>
                </div> : null}
            <div className={"row gx-2 gx-xl-3"}>
                {myAssetsData && myAssetsData.assets && myAssetsData.assets.length > 0 ? myAssetsData.assets.map((asset, index) => {
                    const modifiedNetworkId = asset.networkId == 18 ? 2 : asset.networkId
                    const socialNetwork = networkData.filter((network) => network.id == modifiedNetworkId)
                    return <div key={`post_data${asset.id}`} className="assetsItem" data-id={asset.id}>
                        <ContextMenuTrigger id={`cm_assets_dropDown-${index}`}
                            onContextMenu={(e) => {
                                e.preventDefault();
                            }}>
                            <div className={`card card-custom gutter-b${asset.isChecked ? ' active' : ''}`}>
                                <div className="card-body d-flex flex-column p-2">
                                    <div className="d-flex align-items-center justify-content-between pb-2">
                                        <MediaOptions postPinToTop={postPinToTop} postPostMove={postPostMove} postPostCopy={postPostCopy} postPostSaveCopy={postPostSaveCopy} postPostUpdate={postPostUpdate} asset={asset} addTagModalShow={this.addTagModalShow} rightsHandleModalShow={this.rightsHandleModalShow} editMediaModalShow={this.editMediaModalShow} handleModalShow={this.handleModalShow} deleteAsset={this.deleteAsset} onClickToSingleCheck={onClickToSingleCheck} updateIndex={index} exportDonwloadMedia={this.exportDonwloadMedia} collections={collections} history={this.props.history} postStatusUpdate={postStatusUpdate} />
                                        <ContextMenuTrigger id={`cm_assets_dropDown-${index}`}
                                            onContextMenu={(e) => {
                                                e.preventDefault();
                                            }}
                                            mouseButton={0}>
                                            <i className="ri-more-fill icon-5"></i>
                                        </ContextMenuTrigger>
                                    </div>
                                    <div className="d-flex align-items-center cursor-pointer" onClick={this.handleModalShow(asset, index)}>
                                        <Media asset={asset} animation={'fade'} socialNetwork={socialNetwork} key={asset.publishImage} />
                                    </div>
                                </div>
                            </div>
                        </ContextMenuTrigger>
                        <ContextMenu className="dropdown-menu show context_menu px-0" id={`cm_assets_dropDown-${index}`}>
                            <ContextMenus collections={collections} postPostCopy={postPostCopy} editMediaModalShow={this.editMediaModalShow} handleModalShow={this.handleModalShow} asset={asset} rightsHandleModalShow={this.rightsHandleModalShow} exportDonwloadMedia={this.exportDonwloadMedia} postPostMove={postPostMove} deleteAsset={this.deleteAsset} history={this.props.history} addTagModalShow={this.addTagModalShow} isCollection={isCollection} />
                        </ContextMenu>
                    </div>
                }) : <NoAssetsFound onClickToOpenUpload={this.onClickToOpenUpload} />}
            </div>

            <Suspense fallback={<></>}>
                <FilterAside {...this.props} />
            </Suspense>
            <Suspense fallback={<></>}>
                {modalAssetData ? <AssetsModal modalCallback={this.handleCallback} modalToggle={showModal} modalAssetData={modalAssetData} exportDonwloadMedia={this.exportDonwloadMedia} rightsHandleModalShow={this.rightsHandleModalShow} addTagModalShow={this.addTagModalShow} postPostCopy={postPostCopy} deleteAsset={this.deleteAsset} postPostUpdate={postPostUpdate} collections={collections} history={this.props.history} editMediaModalShow={this.editMediaModalShow} postPostMove={postPostMove} slideAssets={this.slideAssets} currentIndex={this.state.currentIndex} updateAssetStatus={this.updateAssetStatus} /> : null}
            </Suspense>
            <Suspense fallback={<></>}>
                {modalAssetData ? <EditMediaModal editMediaCallback={this.editMediaCallback} editMediaModal={editMediaShowModal} modalAssetData={modalAssetData} /> : null}
            </Suspense>
            <Suspense fallback={<></>}>
                {rightsShowModal ? <GetRightsModal rightsModalCallback={this.rightsHandleCallback} rightsModalToggle={rightsShowModal} modalAssetData={modalAssetData} networkData={networkData} bulkRightsData={bulkRightsData} onUpdateRejectedRightDetail={this.onUpdateRejectedRightDetail} /> : null}
            </Suspense>
            <Suspense fallback={<></>}>
                {addTagShowModal ? <AddTagModal addTagCallback={this.addTagCallback} addTagShowModal={addTagShowModal} modalAssetData={modalAssetData} /> : null}
            </Suspense>
        </>
    }
}



const mapStateToProps = (state) => {
    return {
        networkData: state.networks.networkData,
        uploadStatus: state.modalstatus.uploadStatus
    }
}
const mapDispatchToProps = dispatch => {
    return {
        postMoveToTrash: (data, callback) => dispatch(postMoveToTrash(data, callback)),
        MoveToTrashAll: (callback) => dispatch(MoveToTrashAll(callback)),
        CopyAll: (destination, callback) => dispatch(CopyAll(destination, callback)),
        MoveToAll: (destination, callback) => dispatch(MoveToAll(destination, callback)),
        ClearSelection: () => dispatch(ClearSelection()),
        uploadModalClick: (status) => dispatch(uploadModalClick(status)),
        onAssetToAllReSelect: () => dispatch(onAssetToAllReSelect()),
        statusUpdateAll: (status) => dispatch(statusUpdateAll(status))

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Masonry);